import { Suspense } from 'react';
import { Canvas} from '@react-three/fiber';
import { useGLTF, OrbitControls, Preload, ContactShadows, Lightformer, Environment } from '@react-three/drei'
import CanvasLoader from './CanvasLoader';

 function App() {

  return (
    <div className='w-full h-full canvas-div'>
    <Canvas 
        gl={{ antialias: false, logarithmicDepthBuffer: true }}
        frameloop='demand'
        shadows 
        camera={{ position: [-15, 0, 10], fov: 25 }}>
        <Suspense 
          fallback={<CanvasLoader />}
        > 
          <Scene />
          <OrbitControls/>
          <Env />
          <Preload all />
        </Suspense> 
        </Canvas>
    </div>
  )
}

function Scene() {
  const { scene } = useGLTF(`${process.env.PUBLIC_URL}/models/3.glb`)
  return (<primitive
        receiveShadow
        object={scene}
        scale={0.04} 
        position={[0, -3, 0]}
        rotation={[0, 1.2, 0]}
    />
  )
}

function Env(){
  return(
    <Environment>
        {/* Ceiling */}
        <Lightformer intensity={20} rotation-y={-Math.PI / 2} position={[0, 16, 0]} scale={[100, 2, 1]} />
        {/* Key */}
        <Lightformer form="ring" color="white" intensity={20} scale={2} position={[10, 5, 10]} onUpdate={(self) => self.lookAt(0, 0, 0)} />
      </Environment>
  )
}
useGLTF.preload(`${process.env.PUBLIC_URL}/models/3.glb`);

export default App
